import * as R from 'ramda'

export const argList = R.unapply(R.identity)
export const override = R.flip(R.merge)

export const isNilOrEmpty = R.either(R.isNil, R.isEmpty)
export const isNotNil = R.complement(R.isNil)
export const isNotEmpty = R.complement(R.isEmpty)
export const isNotNilOrEmpty = R.both(isNotNil, isNotEmpty)

export const normalizeBy = (key, items) => {
  const keys = R.pluck(key, items)
  const itemsByKey = R.indexBy(R.prop(key), items)
  return { keys, itemsByKey }
}

const DEFAULT_ENUM_OPTIONS = {
  keyFn: R.identity,
  valueFn: R.identity,
}

export const defineEnums = R.curryN(2, (options, keys) => {
  const { keyFn, valueFn } = R.merge(DEFAULT_ENUM_OPTIONS, options || {})
  return R.reduce(
    (acc, key) => R.assoc(keyFn(key), valueFn(key), acc),
    {},
    keys || []
  )
})
